<!--文章分享模板组件-->
<template>
  <div class="wow slideInLeft">
    <!--数据部分-->
    <article-lists  :AllArticle="AllArticle"></article-lists>
    <!-- 分页部分-->
    <el-pagination
      background
      @current-change="change"
      :current-page.sync="curPage"
      :page-size="pageSize"
      layout="prev, pager, next"
      :total="count"
      >
    </el-pagination>
  </div>
</template>

<script>
import WOW from 'wowjs';
import ArticleLists from '../components/articleLists/ArticleLists.vue'
  export default {
  inject: ['reload'],
  components: { ArticleLists },
  data() {
    return {
      //文章数据列表
      AllArticle:[
          {
        commentNumber: '2',//文章评论数
        description: "SpringMVC 中 @ControllerAdvice 注解的三种使用场景",//文章摘要
        id: '1',//文章id
        imageUrl: "https://api.adicw.cn/images/StudyImg/60a466fd01267.jpg",//首图地址
        title: "@ControllerAdvice 注解的使用场景",//文章标题
        updateTime: "2021-10-27 14:39:25",//发布时间
        views: '15',//浏览量
        }
      ],
      //搜索条件
      form:{
        title:this.$route.query.title,//文章标题
        typeId:this.$route.query.key,//分类id 从访问路径上获取
        tagId:this.$route.query.tag, //标签id
      },
      // key:this.$route.query.key,
      // 当前的页，默认为第一页
      curPage:1,
      // 设置每页的数量
      pageSize:6,
      // 全部文章数量
      count:0,
    }
  },
   beforeRouteUpdate (to, from, next) {
     this.reload()
     next()
  },
  watch:{
    key(val){
        alert(val)
    }
  },
  methods:{
    //改变页面触发方法
    change(val){
      this.GetAllArticle(val,this.pageSize);
    },
    //获取文章列表数据方法
    GetAllArticle(curPage,pageSize){
        this.$http.post('/blog/queryWebBlogAllPage',
            {searchCondition:this.form,pageNum:curPage,pageSize:pageSize}).then(res => {
          // 获取文章数组
          this.AllArticle = res.data.data.queryWebBlogResponse.results;
          //设置总记录数
          this.count=res.data.data.queryWebBlogResponse.total
          //设置当前页显示条数
          this.pageSize=res.data.data.queryWebBlogResponse.pageSize
          //设置当前页码
          this.curPage=res.data.data.queryWebBlogResponse.currentPage
          this.form={}
        })

    },
  },
  created(){
    // 分页请求
    this.GetAllArticle(this.curPage,this.pageSize);
  },
  mounted(){
    //页面加载效果
    let wow = new WOW.WOW({
          boxClass: 'wow',
          animateClass: 'animated',
          offset: 0,
          mobile: true,
          live: true
        });
        wow.init();
  }
  }
</script>

<style lang="scss" scoped>
.el-pagination {
  margin-top: 15px;
  display: flex;
  justify-content: center;
}
</style>
